@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  color: inherit;
  text-decoration: inherit;
}

.App{
  /* background: rebeccapurple; */
  max-width: 100vw;
}
.page {
  padding: 0 5%;
  display: flex;
  min-height: 88vh;
  margin-top: 10vh;
  /* overflow-x: hidden; */
}

.header {
  background: white;
  height: 10vh;
  position: fixed;
  top: 0;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid lightgrey;
  z-index: 100;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.logo-section {
  padding: 0 2%;
  height: 60%;
}

.logo {
  height: 100%;
}

.navlinks-section {
  padding: 0 2%;
  height: 100%;
  display: flex;
  align-items: center;
}

.navlink {
  margin: 0 8px;
  font-size: 16px;
  transition-duration: 0.4s;
}
.navlink:hover {
  color: #85b14a;
}

.activenavlink {
  transition-duration: 0.4s;
  color: #3a7d42;
}
.activenavlink:hover {
  color: #3a7d42;
  text-decoration: none;
}

.footer {
  border-top: 1px solid lightgrey;
  background: white;
  bottom: 0;
  display: flex;
  flex-direction: column;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.footer-body {
  /* background: lavenderblush; */
  padding: 20px;
  display: flex;
  justify-content: space-evenly;
  font-size: 14px;
}

.footer-body-main-section {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-body-section {
  /* background: lavender; */
  display: flex;
  flex-direction: column;
}

.footer-body-section-heading {
  font-size: 18px;
  font-weight: 500;
  margin: 9px 0;
  text-decoration: underline;
}

.footer-body-section-navlink {
  margin: 3px 0;
}

.footer-copyright-section {
  padding: 10px 5%;
  background: whitesmoke;
  border-top: 1px solid lightgrey;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 10px;
  color: gray;
}

@media only screen and (max-width: 600px) {
  .footer-body {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .footer-body-section {
    margin: 10px 0;
  }

  .footer-copyright-section {
    flex-direction: column;
  }

  .footer-copyright {
    margin: 5px 0;
  }
}

.homepage {
  /* background: lavenderblush; */
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
}

.homepage-section {
  height: 90vh;
}

.hero-section {
  height: 80vh;
  width: 100vw;
  /* margin-top: -2vh; */
}

.slider {
  height: 100%;
  width: 100%;
  display: flex;
}

.each-slide {
  height: 70vh;
  display: flex;
  width: 100%;
}

.each-slide-image {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-end;
  color: white;
  transition-duration: 0.4s;
}

.each-slide-image-info {
  margin: 50px;
  background: black;
  min-height: 50px;
  padding: 10px 20px;
  min-width: 240px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.each-slide-image-info-title {
  font-weight: 500;
}

.each-slide-image-info-location {
  font-size: 12px;
}

.each-slide-image-info-learnmore {
  font-size: 10px;
  margin: 4px;
  width: 100%;
  text-align: right;
  fill: white;
}

.each-slide-image-info-learnmore-icon {
  height: 12px;
  width: 12px;
}

.error-page {
  display: flex;
  width: 100vw;
  height: 90vh;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.error-code {
  height: 25vh;
}

.error-page-back-button {
  outline: none;
  font-weight: 600;
  margin: 35px 0;
  cursor: pointer;
  padding: 10px;
  transition-duration: 0.4s;
  border-radius: 4px;
  color: white;
  background: #3a7d42;
  border: 2px solid white;
}

.error-page-back-button:hover {
  border: 2px solid #3a7d42;
  color: #3a7d42;
  background: white;
}

