.footer {
  border-top: 1px solid lightgrey;
  background: white;
  bottom: 0;
  display: flex;
  flex-direction: column;
  user-select: none;
}

.footer-body {
  /* background: lavenderblush; */
  padding: 20px;
  display: flex;
  justify-content: space-evenly;
  font-size: 14px;
}

.footer-body-main-section {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-body-section {
  /* background: lavender; */
  display: flex;
  flex-direction: column;
}

.footer-body-section-heading {
  font-size: 18px;
  font-weight: 500;
  margin: 9px 0;
  text-decoration: underline;
}

.footer-body-section-navlink {
  margin: 3px 0;
}

.footer-copyright-section {
  padding: 10px 5%;
  background: whitesmoke;
  border-top: 1px solid lightgrey;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 10px;
  color: gray;
}

@media only screen and (max-width: 600px) {
  .footer-body {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .footer-body-section {
    margin: 10px 0;
  }

  .footer-copyright-section {
    flex-direction: column;
  }

  .footer-copyright {
    margin: 5px 0;
  }
}
